import {
	IonButtons,
	IonContent,
	IonHeader,
	IonLabel,
	IonButton,
	IonItem,
	IonInput,
	IonCheckbox,
	IonMenuButton,
	IonPage,
	IonTitle,
	IonToolbar,
	IonModal
} from "@ionic/react";
import { useParams } from "react-router";
import React, { useEffect, useState } from "react";
import ExploreContainer from "../components/ExploreContainer";
// import "./about.css";
import flag1 from "../img/french-polynesia.svg";
import flag2 from "../img/en.svg";
import user from "../img/user.svg";
import summary1 from "../img/proffessional-4.jpg";
import summary2 from "../img/overview.jpg";
import summary3 from "../img/about.jpeg";
import summary4 from "../img/about-sec1.png";
import summary5 from "../img/avatar.jpg";
import shape from "../img/shape.png";
import userimg from "../img/user.svg";
import overview from "../img/about2.jpeg";
import handshake from "../img/handshake.png";
import counting from "../img/counting.png";
import speak from "../img/speak.png";
import avatar from "../img/proffessional-2.jpg";
import { useHistory } from "react-router";
import { BASE_URL, BASE_URL_IMAGES, GETUSERBYID } from "../util/constants";
import axios from "axios";

var neworders: any = "0";
const UserSidebar: React.FC = () => {
	const history = useHistory();
	const [ showdetails, setshowdetails ] = useState<any>("");
	const [ count, setCount ] = useState(0);
	const [ page, setpage ] = useState<any>();

	useEffect(
		() => {
			var data: any;
			data = history.location.state;
			if (data == undefined) {
				setpage("dashboard");
			} else {
				setpage(data.pagename);
			}

			axios
				.post(
					`${BASE_URL}${GETUSERBYID}`,
					{
						userid: localStorage.getItem("USER_ID")
					},
					{
						headers: {
							"Content-Type": "application/json",
							Accept: "application/json"
						}
					}
				)
				.then((responseJson) => {
					if (responseJson.data.status == 200) {
						setshowdetails(responseJson.data.data);
					}
				});
			setCount(100);
		},
		[ count ]
	);

	return (
		<div className="col-md-4 col-lg-3">
			<div className="sidebar-wraper">
				<div className="sidebar-bg-image" />
				<div className="user-sidebar">
					<div className="avatar-upload">
						<div className="avatar-preview">
							{showdetails.image != null && showdetails.image != undefined && showdetails.image != "" ? (
								<div
									className="imagePreview"
									style={{ backgroundImage: `url(${BASE_URL_IMAGES}${showdetails.image})` }}
								/>
							) : (
								<div className="imagePreview" />
							)}
						</div>
					</div>
					<h4>{showdetails.fullname}</h4>
					<p>{showdetails.emailaddress}</p>
				</div>
				<ul>
					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push({
									pathname: "/dashboard",
									state: { pagename: "dashboard" }
								});
							}}
							className={`${page == "dashboard" ? "active" : ""}`}
						>
							{" "}
							<i className="fas fa-home" />Dashboard
						</a>{" "}
					</li>
					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push({
									pathname: "/user-orders",
									state: { pagename: "user-orders" }
								});
							}}
							className={`${page == "user-orders" ? "active" : ""}`}
						>
							<i className="fas fa-layer-group" />Orders
						</a>
					</li>

					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push({
									pathname: "/quotes-replies",
									state: { pagename: "quotes-replies" }
								});
							}}
							className={`${page == "reviews" ? "active" : ""}`}
						>
							<i className="far fa-star" />Quotes Replies
						</a>
					</li>

					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push({
									pathname: "/messages",
									state: { pagename: "messages" }
								});
							}}
							className={`${page == "messages" ? "active" : ""}`}
						>
							<i className="far fa-envelope" />Messages
						</a>
					</li>
					{/* <li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push({
									pathname: "/reviews",
									state: { pagename: "reviews" }
								});
							}}
							className={`${page == "reviews" ? "active" : ""}`}
						>
							<i className="far fa-star" />Reviews
						</a>
					</li> */}
					{/* <li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push({
									pathname: "/transactions",
									state: { pagename: "transactions" }
								});
							}}
							className={`${page == "transactions" ? "active" : ""}`}
						>
							<i className="far fa-money-bill-alt" />Transactions
						</a>
					</li> */}
					{/* <li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push({
									pathname: "/payment-cards",
									state: { pagename: "payment-cards" }
								});
							}}
							className={`${page == "payment-cards" ? "active" : ""}`}
						>
							<i className="far fa-credit-card" />Payment Cards
						</a>
					</li> */}
					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push({
									pathname: "/notifications",
									state: { pagename: "notifications" }
								});
							}}
							className={`${page == "notifications" ? "active" : ""}`}
						>
							<i className="far fa-bell" />Notifications
						</a>
					</li>
					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push({
									pathname: "/profile-settings",
									state: { pagename: "profile-settings" }
								});
							}}
							className={`${page == "profile-settings" ? "active" : ""}`}
						>
							<i className="fas fa-user-cog" />Profile Settings
						</a>
					</li>
				</ul>
			</div>
		</div>
	);
};

export default UserSidebar;
