import React from 'react';
import { Route, Redirect } from "react-router-dom";

const HomeGuardedRoute = ({ component: Component, auth, ...rest }) => {

auth = localStorage.getItem('HAS_LOGGED_IN');
if(auth){
    auth = true
    console.log("auth:" + auth);
}else{
    auth = false
}

    return(
        <Route {...rest} render={(props) => (
            auth === true
                ?  <Component {...props} /> 
                : <Redirect to='/login' />
        )} />
    )
}

export default HomeGuardedRoute;