  const [count, setCount] = useState(0);
  const [allmessagelist, setallmessagelist] = useState([]);
  const [currentchat, setCurrentChat] = useState<any>(null);
  const [messages, setMessages] = useState<any>(null);
  const [newmessage, setnewmessage] = useState<any>(null);
  const [arrivalMessage, setArrivalMessage] = useState<any>(null);
  const [onlineUsers, setOnlineUsers] = useState<any>([]);
  const scrollRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    socket = io('http://54.234.126.233:3000');
    socket.on("getMessage", (data:any) => {
      console.log(data);
      setArrivalMessage({
        sender: data.senderId,
        text: data.text,
        createdAt: Date.now(),
      });
    });
  }, []);

  useEffect(() => {
    arrivalMessage &&
      currentchat?.members.includes(arrivalMessage.sender) &&
      setMessages((prev:any) => [...prev, arrivalMessage]);
  }, [arrivalMessage, currentchat]);

  useEffect(() => {
    var user = localStorage.getItem('SP_ID');
    socket.emit("addUser", user);
    socket.on("getUsers", (users:any) => {
      console.log("GET USERS");
      console.log(users)
      setOnlineUsers(
        users.filter((f:any) => users.some((u:any) => u.userId === f))
      );      
    });
  }, [user]);

  useEffect(() => {
    _getUserConversations();
    setCount(100);
  }, [count]);

  useEffect(() => {
    if(currentchat != null && currentchat != undefined && currentchat != ''){
      console.log(currentchat);
      var id = currentchat.chatid;
      fetch(`${BASE_URL}${GETMESSAGESBYCONVERSATIONID}/${id}`).then(response => response.json()).then(json => {
        setMessages(json);
      })
    }
  },[currentchat])

  useEffect(() => {
    const node = scrollRef.current;
    node?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

