import React, { useState, useEffect } from "react";

import { IonApp, IonRouterOutlet, IonSplitPane } from "@ionic/react";
import { IonReactRouter } from "@ionic/react-router";
// import { Redirect, Route } from "react-router-dom";

import { BrowserRouter as Router, Route, Redirect } from "react-router-dom";

import Menu from "./components/Menu";
import Page from "./pages/Page";
import Error from "./pages/Error";

import Home from "./pages/home";
import Login from "./pages/login";
import Signup from "./pages/signup";
import Forgotpassword from "./pages/forgotpassword";
import ProfessionalLogin from "./pages/professional-login";

import About from "./pages/about";
import WhyChoosingUs from "./pages/why-choosing-us";
import Contact from "./pages/contact";
import Faq from "./pages/faq";
import PrivacyNotice from "./pages/privacy-notice";
import TermsUse from "./pages/terms-use";
import HowItWorks from "./pages/how-it-works";
import HowWorksProvider from "./pages/how-works-provider";
import Blog from "./pages/blog";
import Dashboard from "./pages/dashboard";
import BlogDetails from "./pages/blog-details";
import Service from "./pages/service";
import PaymentCards from "./pages/payment-cards";
import AddCard from "./pages/add-card";
import Messages from "./pages/messages";
import ProfessionalMessages from "./pages/professional-messages";
import ProfessionalOrders from "./pages/professional-orders";
import ProfessionalPaycards from "./pages/professional-paycards";
import ProfessionalTransactions from "./pages/professional-transactions";
import JobOrders from "./pages/job-orders";
import ProfessionalDashboard from "./pages/professional-dashboard";
import AddBusiness from "./pages/add-business";
import Pricing from "./pages/pricing";
import Calendar from "./pages/calendar";
import Categories from "./pages/categories";
import UserOrders from "./pages/user-orders";
import CustomerReviews from "./pages/customer-reviews";
import ServiceDetails from "./pages/service-details";
import ServiceDetailsLogin from "./pages/service-details-login";
import Notifications from "./pages/notifications";
import ProfessionalNotifications from "./pages/professional-notifications";
import ProfessionalSettings from "./pages/professional-settings";
import ProfessionalQuotes from "./pages/professional-quotes";

import UserQuotes from "./pages/quotes-replies";
import ProfessionalQuotesReplies from "./pages/professional-quote-replies";

import Paypal from "./pages/paypal";

import Transactions from "./pages/transactions";
import ProfileSettings from "./pages/profile-settings";
import Reviews from "./pages/reviews";
import GuardedRoute from "./GuardedRoute";
import HomeGuardedRoute from "./HomeGuardedRoute";

/* Core CSS required for Ionic components to work properly */
import "@ionic/react/css/core.css";

/* Basic CSS for apps built with Ionic */
import "@ionic/react/css/normalize.css";
import "@ionic/react/css/structure.css";
import "@ionic/react/css/typography.css";

/* Optional CSS utils that can be commented out */
import "@ionic/react/css/padding.css";
import "@ionic/react/css/float-elements.css";
import "@ionic/react/css/text-alignment.css";
import "@ionic/react/css/text-transformation.css";
import "@ionic/react/css/flex-utils.css";
import "@ionic/react/css/display.css";

/* Theme variables */
import "./theme/variables.css";

const App: React.FC = () => {
	const [ isAutheticated, setisAutheticated ] = useState(false);

	const onScreenLoad = () => {
		const authenticated_user = localStorage.getItem("HAS_LOGGED_IN");
		console.log(authenticated_user);
		if (authenticated_user) {
			setisAutheticated(true);
		} else {
			setisAutheticated(false);
		}
	};

	const checkauth = () => {
		const authenticated_user = localStorage.getItem("HAS_LOGGED_IN");
		console.log(authenticated_user);
		if (authenticated_user) {
			return true;
		} else {
			return false;
		}
	};

	useEffect(() => {
		console.log("ENTER");
		onScreenLoad();
	}, []);

	return (
		<Router>
			<IonApp>
				<IonReactRouter>
					<IonSplitPane contentId="main">
						<Menu />
						<IonRouterOutlet id="main">
							{/* <Route path="/" exact={true}>
							<Redirect to="/signup" />
							</Route> */}

							<GuardedRoute path="/" component={Signup} exact={true} auth={isAutheticated} />

							<Route path="/page/:name" exact={true}>
								<Page />
							</Route>

							<Route path="/error" exact={true}>
								<Error />
							</Route>

							<Route path="/service-details" exact={true}>
								<ServiceDetails />
							</Route>

							<Route path="/about" exact={true}>
								<About />
							</Route>

							<Route path="/blog-details" exact={true}>
								<BlogDetails />
							</Route>

							<Route path="/blog" exact={true}>
								<Blog />
							</Route>

							<Route path="/contact" exact={true}>
								<Contact />
							</Route>

							<Route path="/pricing" exact={true}>
								<Pricing />
							</Route>

							<Route path="/add-business" exact={true}>
								<AddBusiness />
							</Route>

							<Route path="/faq" exact={true}>
								<Faq />
							</Route>

							<Route path="/paypal" exact={true}>
								<Paypal />
							</Route>

							{/* <Route path="/forgotpassword" exact={true}>
						<Forgotpassword />
						</Route> */}

							<GuardedRoute
								path="/forgotpassword"
								component={Forgotpassword}
								exact={true}
								auth={isAutheticated}
							/>

							{/* <HomeGuardedRoute path="/home" component={Home} exact={true} auth={checkauth()} /> */}

							<Route path="/home" exact={true}>
								<Home />
							</Route>

							{/* 
						<GuardedRoute path="/login" component={Login} exact={true} auth={isAutheticated} /> */}

							<Route path="/login" exact={true}>
								<Login />
							</Route>

							<Route path="/messages" exact={true}>
								<Messages />
							</Route>

							<Route path="/professional-login" exact={true}>
								<ProfessionalLogin />
							</Route>

							<Route path="/professional-messages" exact={true}>
								<ProfessionalMessages />
							</Route>

							<Route path="/payment-cards" exact={true}>
								<PaymentCards />
							</Route>

							<Route path="/privacy-notice" exact={true}>
								<PrivacyNotice />
							</Route>

							<Route path="/service" exact={true}>
								<Service />
							</Route>

							<Route path="/signup" exact={true}>
								<Signup />
							</Route>

							<Route path="/add-card" exact={true}>
								<AddCard />
							</Route>

							<Route path="/terms-use" exact={true}>
								<TermsUse />
							</Route>

							<Route path="/how-it-works" exact={true}>
								<HowItWorks />
							</Route>

							<Route path="/how-works-provider" exact={true}>
								<HowWorksProvider />
							</Route>

							<Route path="/dashboard" exact={true}>
								<Dashboard />
							</Route>

							<Route path="/why-choosing-us" exact={true}>
								<WhyChoosingUs />
							</Route>

							<Route path="/notifications" exact={true}>
								<Notifications />
							</Route>

							<Route path="/professional-notifications" exact={true}>
								<ProfessionalNotifications />
							</Route>

							<Route path="/professional-dashboard" exact={true}>
								<ProfessionalDashboard />
							</Route>

							<Route path="/transactions" exact={true}>
								<Transactions />
							</Route>

							<HomeGuardedRoute
								path="/user-orders"
								component={UserOrders}
								exact={true}
								auth={checkauth()}
							/>

							<Route path="/profile-settings" exact={true}>
								<ProfileSettings />
							</Route>

							<Route path="/professional-paycards" exact={true}>
								<ProfessionalPaycards />
							</Route>

							<Route path="/professional-orders" exact={true}>
								<ProfessionalOrders />
							</Route>

							<Route path="/professional-settings" exact={true}>
								<ProfessionalSettings />
							</Route>

							<Route path="/job-orders" exact={true}>
								<JobOrders />
							</Route>

							<Route path="/calendar" exact={true}>
								<Calendar />
							</Route>

							<Route path="/categories" exact={true}>
								<Categories />
							</Route>

							<Route path="/customer-reviews" exact={true}>
								<CustomerReviews />
							</Route>

							<Route path="/service-details-login" exact={true}>
								<ServiceDetailsLogin />
							</Route>

							<Route path="/professional-transactions" exact={true}>
								<ProfessionalTransactions />
							</Route>

							<Route path="/professional-quotes" exact={true}>
								<ProfessionalQuotes />
							</Route>

							<Route path="/quotes-replies" exact={true}>
								<UserQuotes />
							</Route>

							<Route path="/professional-quote-replies" exact={true}>
								<ProfessionalQuotesReplies />
							</Route>

							<Route path="/reviews" exact={true}>
								<Reviews />
							</Route>
						</IonRouterOutlet>
					</IonSplitPane>
				</IonReactRouter>
			</IonApp>
		</Router>
	);
};

export default App;
