import { IonButtons, IonContent, IonHeader, IonTextarea, IonSelectOption , IonSelect , IonButton, IonItem, IonInput, IonCheckbox, IonMenuButton, IonPage, IonTitle, IonToolbar } from '@ionic/react';
import { useParams } from 'react-router';
import React, { useEffect, useState, useRef } from "react";
import ExploreContainer from '../components/ExploreContainer';
import UserHeader from '../components/user-header';
import './profile-settings.css';
import flag1 from '../img/french-polynesia.svg';
import flag2 from '../img/en.svg';
import user from '../img/user.svg';
import summary1 from "../img/about.jpeg";
import summary2 from "../img/about-sec1.png";
import summary3 from "../img/fisherman.png";
import summary4 from "../img/proffessional-4.jpg";
import summary5 from "../img/overview.jpg";
import shape from "../img/shape.png";
import overview from "../img/about2.jpeg"; 
import handshake from '../img/handshake.png';
import counting from '../img/counting.png';
import speak from '../img/speak.png';
import avatar from '../img/avatar.jpg';
import { useHistory } from "react-router";

import { BASE_URL, BASE_URL_IMAGES, CHATLISTBYUSER, GETUSERBYID, GETMESSAGESBYCONVERSATIONID, SENDNEWMESSAGE, GETPROFESSIONAL } from '../util/constants';
import axios from 'axios';
import { format } from 'timeago.js';
import  io  from "socket.io-client";
import Loader from "react-js-loader";
var socket:any = null;
const ProfileSettings: React.FC = () => {
const history = useHistory();

  const { name } = useParams<{ name: string; }>();
  const [count, setCount] = useState(0);
  const [allmessagelist, setallmessagelist] = useState([]);
  const [currentchat, setCurrentChat] = useState<any>(null);
  const [messages, setMessages] = useState<any>(null);
  const [newmessage, setnewmessage] = useState<any>(null);
  const [arrivalMessage, setArrivalMessage] = useState<any>(null);
  const [onlineUsers, setOnlineUsers] = useState<any>([]);
  // const [socket, setSocket] = useState<any>(null);
  const scrollRef = useRef<HTMLDivElement>(null);
  const [language, setlanguage] = useState('fr');
  const [loader, setloader] = useState<any>(true);

  useEffect(()=>{
    var lang = localStorage.getItem('lookvast_lang');
    if(lang != null && lang != undefined && lang != '')
    setlanguage(lang);
  },[])

  useEffect(() => {
    // setSocket(io ('http://54.234.126.233:3000'));
    socket = io('http://54.234.126.233:3000');
    socket.on("getMessage", (data:any) => {
      console.log(data);
      setArrivalMessage({
        sender: data.senderId,
        text: data.text,
        createdAt: Date.now(),
      });
    });
  }, []);

  useEffect(() => {
    arrivalMessage &&
      currentchat?.members.includes(arrivalMessage.sender) &&
      setMessages((prev:any) => [...prev, arrivalMessage]);
  }, [arrivalMessage, currentchat]);

  useEffect(() => {
    var user = localStorage.getItem('USER_ID');
    socket.emit("addUser", user);
    socket.on("getUsers", (users:any) => {
      console.log("GET USERS");
      console.log(users)
      // setOnlineUsers(
      //   users.followings.filter((f:any) => users.some((u:any) => u.userId === f))
      // );
    });
  }, [user]);



  useEffect(() => {
    _getUserConversations();
    setCount(100);
  }, [count]);

  useEffect(() => {
    if(currentchat != null && currentchat != undefined && currentchat != ''){
      console.log(currentchat);
      var id = currentchat.chatid;
      fetch(`${BASE_URL}${GETMESSAGESBYCONVERSATIONID}/${id}`).then(response => response.json()).then(json => {
        setMessages(json);
      })
    }
  },[currentchat])

  useEffect(() => {
    const node = scrollRef.current;
    node?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const handleSubmit = async (e:any) => {
    console.log(currentchat);
    e.preventDefault();
    if(newmessage != null && newmessage != '' && newmessage != undefined){
      const nmessage = {
        sender: localStorage.getItem('USER_ID'),
        text: newmessage,
        conversationId: currentchat.chatid
      }

      const receiverId = currentchat.members.find((member:any) => member !== localStorage.getItem('USER_ID'));

      socket.emit("sendMessage", {
        senderId: localStorage.getItem('USER_ID'),
        receiverId,
        text: newmessage,
      });

      try{
        const res = await axios.post(`${BASE_URL}${SENDNEWMESSAGE}`, nmessage);
        setMessages([...messages, res.data]);
        setnewmessage('');
      }catch(err){
        console.log(err);
      }
    }
  }

  const _getUserConversations = () => {
      var arr:any = [];
      setloader(true);
      fetch(`${BASE_URL}${CHATLISTBYUSER}/${localStorage.getItem('USER_ID')}`).then(response => response.json()).then(json => {
        if(json.length > 0){
        json.forEach((element:any) => {
            const providerid = element.members.find((m:any)=> m !== localStorage.getItem('USER_ID'));
            axios.post(
              `${BASE_URL}${GETPROFESSIONAL}`,
              {
                id: providerid
              },
              {
                headers: {
                  "Content-Type": "application/json",
                  Accept: "application/json"
                }
              }
            )
            .then((responseJson) => {
              if (responseJson.data.status == 200) {
                setloader(false);
                arr.push({
                  username: responseJson.data.data.companyname,
                  conversationid: element._id,
                  email: responseJson.data.data.email,
                  image: responseJson.data.data.image,
                  members: element.members
                })
                setallmessagelist(arr);
              }
            });

        });
        }else{
          setloader(false);
        }
      })
  }

  const updateLanguage = (val: string):void => {
    setlanguage(val);
  }

  return (
    <IonPage>
      {/* <IonHeader>
        <IonToolbar>
          <IonButtons slot="start">
            <IonMenuButton />
          </IonButtons>
          <IonTitle>{name}</IonTitle>
        </IonToolbar>
      </IonHeader> */}

      <IonContent fullscreen>
        <UserHeader lang={language} updateLanguage={(e:any) => updateLanguage(e)}/>

        {loader ?
        <div className={"row"}>
            <div className={"item"}>
                <Loader type="bubble-top" bgColor={"#008080"}  size={80} />
            </div>
            {/* <div className={"item"}>
                <Loader type="spinner-cub" bgColor={"#00BFFF"} title={"spinner-cub"} size={100} />
            </div> */}
        </div>

        :
        null }

        <div className="page-title-area">
          <div className="d-table">
            <div className="d-table-cell">
              <div className="container">
                <div className="page-title-content">
                  <h2>Messages</h2>
                  <ul>
                    <li><a href="#">Home</a></li>
                    <li>Messages</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
        <section className="user-pannel-dashborad bg-gray ptb-100">
          <div className="container">
            <div className="row">
              <div className="col-md-12">
              <div className="rofle-all-st">
                <div className="container">
                  <div className="cont-all-shoe">
                    <div className="row">
                      
                      <div className="col-md-12">
                        <div className="cont-right-st">
                          <h3 className="main-ttls">Your Messages 
                          <a href="javascript:void(0);" className="pull-right btn-back text-uppercase" onClick={(e) => {e.preventDefault();history.push("/dashboard");}}>
                          Back
                          </a>
                          </h3>
                          
                        <div className="messages-cont">
                            <div className="row">
                              <div className="col-md-4 pr-0">
                                <div className="list-chts">
                                  <div className="heds">
                                    <h5> Messages ({allmessagelist.length}) </h5>
                                  </div>
                                  <ul className="list-usrs mCustomScrollbar"> 
                                  {allmessagelist.length > 0 ?
                                  allmessagelist.map((message:any)=> {
                                    return(
                                      <li> 
                                        <a onClick={()=> setCurrentChat({
                                            chatid: message.conversationid,
                                            chatname: message.username,
                                            chatimage: message.image,
                                            members: message.members
                                          })} className="name-ls">
                                          {/* <span className="onln-dot"></span> */}

                                          {message.image != null && message.image != undefined && message.image != "" ? (
                                            <img src={`${BASE_URL_IMAGES}${message.image}`}/>
                                          ) : (
                                            <img src='http://54.234.126.233:3000/images/1624632511823download2.jpeg' />
                                          )}
                                          <h4> {message.username} </h4>
                                          <p> {message.email}</p>
                                        </a>
                                      </li>
                                    )
                                  })
                                  :
                                  <li> 
                                    <a href="#" className="name-ls">
                                      <h4> No Messages Found! </h4>
                                    </a>
                                  </li>
                                  }

                                  </ul>
                                </div>
                              </div>
                              <div className="col-md-8 pl-0">
                              {currentchat != null ? 
                                <div className="msgs-all">
                                  <div className="hads-es">

                                  {currentchat.chatimage != undefined && currentchat.chatimage != null && currentchat.chatimage != '' ?
                                    <img src={`${BASE_URL_IMAGES}${currentchat.chatimage}`}/>
                                  :
                                    <img src="http://54.234.126.233:3000/images/1624632511823download2.jpeg" />
                                  }
                                   
                                    <h4> {currentchat.chatname} </h4>
                                    {/* <span className="onln"> <i className="fa fa-circle" aria-hidden="true"></i> Online </span> */}
                                  </div>
                                  <div className="chats-alls">
                                    <ul className="list-all-chat mCustomScrollbar">
                                    {messages != null ?
                                    messages.map((m:any)=> {
                                      return(
                                        m.sender == localStorage.getItem('USER_ID') ?
                                        
                                        <li className="right-msg">
                                        <div ref={scrollRef}>
                                          <div className="msg-ic">
                                            {m.text}
                                          </div>
                                          <span className="time-msg"><i className="far fa-clock"></i> {format(m.createdAt)}</span> 
                                        </div>
                                        </li>
                                        
                                        :
                                        
                                        <li>
                                        <div ref={scrollRef}>
                                        {currentchat.chatimage != undefined && currentchat.chatimage != null && currentchat.chatimage != '' ?
                                          <img src={`${BASE_URL_IMAGES}${currentchat.chatimage}`} className="user-img"/>
                                        :
                                          <img src="http://54.234.126.233:3000/images/1624632511823download2.jpeg" className="user-img"/>
                                        }
                                          <div className="msg-ic">
                                           {m.text}
                                          </div>
                                          <span className="time-msg"><i className="far fa-clock"></i> {format(m.createdAt)}</span> 
                                        </div>
                                        </li>
                                        
                                      )
                                    })
                                    :
                                    null
                                    }

                                    
                                    </ul>
                                  </div>
                                  <div className="chat-foots">
                                    <div className="cont-all-set">
                                      <IonInput type="text" className="fld-sa" placeholder="Type message here..."
                                      value={newmessage} 
                                      onIonChange={(e) => {
                                        setnewmessage(e.detail.value!);
                                      }}></IonInput>
                                      <a onClick={handleSubmit} style={{cursor:'pointer'}} className="sbmt-s"> <i className="fa fa-paper-plane"></i> Send </a>
                                      {/* <a href="#" className="icon-s"> <i className="fas fa-camera"></i> </a> */}
                                    </div>
                                  </div>
                                </div>
                                :
                                 <div className="msgs-all">
                                 <p className="startchat">Open the conversation to start Chat!</p>
                                 </div>
                              }
                              </div>
                            </div>
                          </div>
                        </div>
                              </div>
                    </div>
                  </div>
                </div>
              </div>
              </div>
            </div>
          </div>
          {/* <div className="summary-shape-image">
           <img src={shape} />
          </div> */}
        </section>
   	<footer id="footer" className="footer-container">
			<div className="container">
				<div className="row list-bunch">
					<div className="col-lg-4 list-bunch-item ">
						<h3 className="logo-title text-white">LookVast</h3>
						<p className="text-left text-white">Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s,</p>
					</div>
					<div className="col-lg-3 list-bunch-item">
						<h6 className="mb-3">Quick Links</h6>
            <ul className="links">
              <li><a onClick={(e) => {e.preventDefault();history.push("/about");}} href="javascript:void(0);">About</a></li>
              <li><a onClick={(e) => {e.preventDefault();history.push("/why-choosing-us");}} href="javascript:void(0);">Why Choosing us</a></li>
              <li><a onClick={(e) => {e.preventDefault();history.push("/faq");}} href="javascript:void(0);">FAQ</a></li>
              <li><a onClick={(e) => {e.preventDefault();history.push("/contact");}} href="javascript:void(0);">Contact Us</a></li>
              <li><a onClick={(e) => {e.preventDefault();history.push("/privacy-notice");}} href="javascript:void(0);">Privacy Notice</a></li>
              <li><a onClick={(e) => {e.preventDefault();history.push("/terms-use");}} href="javascript:void(0);">Terms of use</a></li>
            </ul>
					</div>
					<div className="col-lg-3 list-bunch-item">
						<h6 className="mb-3">Customers</h6>
						<ul className="links">
							<li><a href="javascript:void(0);" onClick={(e) => {e.preventDefault();history.push("/how-it-works");}}>How it works</a></li>
							<li><a onClick={(e) => {e.preventDefault();history.push("/service");}} href="javascript:void(0);">Find Services</a></li>
							<li><a href="javascript:void(0);">Download App</a></li>
							<li><a onClick={(e) => {e.preventDefault();history.push("/signup");}} href="javascript:void(0);">Sign Up</a></li>
						</ul>
					</div>
					<div className="col-lg-2 list-bunch-item">
						<h6 className="mb-3">Providers</h6>
						<ul className="links">
							<li><a href="javascript:void(0);" onClick={(e) => {e.preventDefault();history.push("/how-works-provider");}}>How does it work</a></li>
							<li><a href="javascript:void(0);">Join our Network</a></li>
							<li><a href="javascript:void(0);">Download App</a></li>
							<li><a onClick={(e) => {e.preventDefault();history.push("/blog");}} href="javascript:void(0);">Blog/Reviews</a></li>
						</ul>
					</div>	
				</div>
			</div>
		</footer>
		<div className="last-footer text-muted">
			<div className="container">
				<div className="row align-items-center">
					<div className="col-lg-6">
						<ul className="social">
							<li><a href="javascript:void(0);"><i className="fab fa-facebook-square"></i></a></li>
							<li><a href="javascript:void(0);"><i className="fab fa-instagram"></i>	</a></li>
							<li><a href="javascript:void(0);"><i className="fab fa-linkedin-in"></i></a></li>
						</ul> 
					</div>
					<div className="col-lg-6 text-right">
						<p> 2021 LookVast. All rights reserved.</p>
					</div>
				</div>
			</div>
		</div>
      </IonContent>
    </IonPage>
  );
};

export default ProfileSettings;
