// @ts-ignore
import { IonButtons, IonContent, IonHeader, IonLabel, IonButton, IonItem, IonInput, IonCheckbox, IonMenuButton, IonPage, IonTitle, IonToolbar, IonText } from '@ionic/react';
import React, { useEffect, useState } from "react";
import { setIsLoggedIn, setUsername } from '../data/user/user.actions';
import { useParams } from 'react-router';
import ExploreContainer from '../components/ExploreContainer';
import './signup.css';
import flag1 from '../img/french-polynesia.svg';
import flag2 from '../img/en.svg';
import user from '../img/user.svg';
import { useHistory } from "react-router";
import 'react-phone-number-input/style.css'
import PhoneInput from 'react-phone-number-input';
import { RouteComponentProps } from 'react-router';
import * as validator from 'validator';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import OtpInput from 'react-otp-input';
import { BASE_URL, SIGNUP, RESENDOTP, VERIFYOTP } from '../util/constants';
import axios from 'axios';

const Signup: React.FC= () => {
const history = useHistory();
  const { name } = useParams<{ name: string; }>();
  const [value, setValue] = useState()


  const [fullname, setFullname] = useState('');
  const [emailaddress, setEmailAddress] = useState('');
  const [phonenumber, setPhonenumber] = useState('');
  const [password, setPassword] = useState('');
  const [cpassword, setCpassword] = useState('');

  const [formSubmitted, setFormSubmitted] = useState(false);

  const [fullnameError, setFullnameError] = useState(false);
  const [emailaddressError, setEmailAddressError] = useState(false);
  const [emailValid, setEmailValidError] = useState(false);
  const [passwordmatchError, setPasswordMatchError] = useState(false);
  const [passwordErrorMessage, setpasswordErrorMessage] = useState(false);
  const [phonenumberError, setPhonenumberError] = useState(false);
  const [passwordError, setPasswordError] = useState(false);
  const [cpasswordError, setCpasswordError] = useState(false);

  const [userid, setuserid] = useState('');
  const [islogin, setislogin] = useState(false);
  const [otp, setotp] = useState('');
  const [otperror, setotperror] = useState(false);




  /**********VALIDATE PASSWORD***** */
  const validatePassword = (value:any) => {
    console.log(value);
    const check = validator.isStrongPassword(value, {
      minLength: 8, minLowercase: 1,
      minUppercase: 1, minNumbers: 1
    })

    if(check){
      return true;
    }else{
      return false;
    }
  }

  /************RESEND OTP*************** */

  const resendOtp = async() => {
    console.log(userid);
    console.log(phonenumber);
    await axios.post(`${BASE_URL}${RESENDOTP}`,{
        'phonenumber': phonenumber,
        'userid': userid,
    },
    {
          headers: {
            'Content-Type': 'application/json',
            Accept: 'application/json',
          },
    },
    ).then(responseJson => {
      console.log(responseJson);
      if(responseJson.data.status == 200){
        toast(responseJson.data.msg);
      }else{
        toast(responseJson.data.msg , {
          type: 'error'
        });
      }
    })
  }


  /************Signup FUNCTION************ */
  const Signup = async (e: React.FormEvent) => {
    console.log(BASE_URL);
    e.preventDefault();
    setFormSubmitted(true);
    if(!fullname) {
      setFullnameError(true);
    }
    if(!emailaddress) {
      setEmailAddressError(true);
    }
    if(!phonenumber) {
      setPhonenumberError(true);
    }
    if(!password) {
      setPasswordError(true);
    }
    if(!cpassword) {
      setCpasswordError(true);
    }



    if(fullname && emailaddress && phonenumber && password && cpassword) {

      if (!validator.isEmail(emailaddress)) {
        setEmailValidError(true);
        return;
      }else{
        setEmailValidError(false);
      }
      
      const validatepass = validatePassword(password);
      if(!validatepass){
        setpasswordErrorMessage(true);
        return;
      }else{
        setpasswordErrorMessage(false);
      } 

      if(password != cpassword){
        setPasswordMatchError(true);
        return;
      }else{
        setPasswordMatchError(false);
      }


      await axios.post(`${BASE_URL}${SIGNUP}`,{
          fullname: fullname,
          emailaddress: emailaddress,
          phonenumber: phonenumber,
          password: password,
      },
      {
            headers: {
              'Content-Type': 'application/json',
              Accept: 'application/json',
            },
      },
      ).then(responseJson => {
        console.log(responseJson);
        if(responseJson.data.status == 200){
          toast("Please Verify your Phone Number and Email first!");
          setuserid(responseJson.data.data._id);
          setTimeout(() => {
            setislogin(true);
            setFormSubmitted(false);
          }, 5000);
        }else{
          var resp = responseJson;
          toast(resp.data.msg , {
            type: 'error'
          });
        }
      })
      .catch(error => {
          toast("Please try again!" , {
            type: 'error'
          });
      });
    }
  };

  /***********VERIFY********** */

  const submitverifications = async (e: React.FormEvent) => {
    e.preventDefault();
    setFormSubmitted(true);
    console.log(otp);
    if(!otp){
      setotperror(true);
      setotp('');
      return;
    }else{
      setotperror(false);
      setotp('');
    }

    await axios.post(`${BASE_URL}${VERIFYOTP}`,{
        'otp': otp,
        'userid': userid,
    },
    {
          headers: {
            'Content-Type': 'application/json',
            Accept: 'application/json',
          },
    },
    ).then(responseJson => {
      console.log(responseJson);
      if(responseJson.data.status == 200){
        toast(responseJson.data.msg);
          setTimeout(() => {
            history.push('/login', {direction: 'none'});
          },3000)
      }else{
        toast(responseJson.data.msg , {
          type: 'error'
        });
      }
    })
  }


  const handleChange = async(e:any) => {
    var value = e;
    setotp(value);
  }



  return (
    <IonPage>
      {/* <IonHeader>
        <IonToolbar>
          <IonButtons slot="start">
            <IonMenuButton />
          </IonButtons>
          <IonTitle>{name}</IonTitle>
        </IonToolbar>
      </IonHeader> */}

      <IonContent fullscreen>
        <ToastContainer autoClose={4000}></ToastContainer>
        <div className="navbar-area bg-white">
          <div className="trifles-responsive-nav">
            <div className="container">
              <div className="trifles-responsive-menu">
                <div className="logo">
                  <a href="javascript:void(0);">
                    <h3 className="logo-title">LookVast</h3>
                  </a>
                </div>
              </div>
            </div>
          </div>
          <div className="trifles-nav">
            <div className="container-fluid px-5">
              <nav className="navbar navbar-expand-md navbar-light">
                <a className="navbar-brand" onClick={(e) => {e.preventDefault();history.push("/home");}} href="javascript:void(0);">
                  <h3 className="logo-title">LookVast</h3>
                </a>
                <div className="collapse navbar-collapse mean-menu" id="navbarSupportedContent">
                  <ul className="navbar-nav">
                   {/* <li className="nav-item active">
                      <a href="javascript:void(0);" className="nav-link" onClick={(e) => {e.preventDefault();history.push("/home");}}>
                      Maison
                      </a>
                    </li> */}
                    <li className="nav-item">
                      <a href="javascript:void(0);" className="nav-link">
                        Aucciel
                        <i className="fa fa-chevron-down"></i>
                      </a>
                        <ul className="dropdown-menu">
                          <li className="nav-item">
                          <a href="javascript:void(0);" className="nav-link active">
                          About
                          </a>
                            </li>
                      <li className="nav-item">
                        <a href="javascript:void(0);" className="nav-link">
                        Privacy
                        </a>
                      </li>
                      <li className="nav-item">
                        <a href="javascript:void(0);" className="nav-link">
                        Contact Us
                        </a>
                      </li>
                      </ul>
                    </li>
                    {/* <li className="nav-item">
                      <a href="javascript:void(0);" className="nav-link">
                      A Propos De
                      </a>
                    </li> */}
                    <li className="nav-item">
                      <a href="javascript:void(0);" className="nav-link">
                        Explorer Les Services
                        <i className="fa fa-chevron-down"></i>
                      </a>
                        <ul className="dropdown-menu">
                          <li className="nav-item">
                          <a href="javascript:void(0);" className="nav-link">
                          Automobile
                          </a>
                            </li>
                      <li className="nav-item">
                        <a href="javascript:void(0);" className="nav-link">
                        Rparation et Rnovation
                        </a>
                      </li>
                      <li className="nav-item">
                        <a href="javascript:void(0);" className="nav-link">
                        Design d'Interieur et Dcoration
                        </a>
                      </li>
                      <li className="nav-item">
                        <a href="javascript:void(0);" className="nav-link">
                        vnementiel
                        </a>
                      </li>
                      <li className="nav-item">
                        <a href="javascript:void(0);" className="nav-link">
                        Beaut et Bien-tre
                        </a>
                      </li>
                      <li className="nav-item">
                        <a href="javascript:void(0);" className="nav-link">
                        Soin des Animaux
                        </a>
                      </li>
                      <li className="nav-item">
                        <a href="javascript:void(0);" className="nav-link">
                        Service de Mnage
                        </a>
                      </li>
                      <li className="nav-item">
                        <a href="javascript:void(0);" className="nav-link">
                        Tourisme  
                        </a>
                      </li>
                      <li className="nav-item">
                        <a href="javascript:void(0);" className="nav-link">
                        Development personnel  
                        </a>
                      </li>
                      <li className="nav-item">
                        <a href="javascript:void(0);" className="nav-link">
                        Development personnel
                        </a>
                      </li>
                      </ul>
                    </li>
                    {/* <li className="nav-item">
                      <a href="javascript:void(0);" className="nav-link">
                      Explorer Les Services
                      </a>
                    </li> */}
                    <li className="nav-item">
                      <a href="javascript:void(0);" className="nav-link">
                      Jainces Pro
                      </a>
                    </li>
                  </ul>
                  <ul className="navbar-nav mr-0 right-menu">
                    <li className="nav-item">
                      <a href="javascript:void(0);" className="nav-link btn btn-primary header-customize-item button langauge-dropdown dropdown-toggle"> 
                      <img src={flag1} alt="Logo" /> 
                      {/* Fr */}
                      </a>
                      <ul className="dropdown-menu">
                        <li className="nav-item">
                          <a href="javascript:void(0);" className="nav-link active">
                          <img src={flag1} alt="Logo" /> 
                          {/* Fr */}
                          </a>
                        </li>
                        <li className="nav-item">
                          <a href="javascript:void(0);" className="nav-link">
                          <img src={flag2} alt="Logo" /> 
                          {/* En */}
                          </a>
                        </li>
                      </ul>
                    </li>
                    <li className="nav-item">
                      <a href="javascript:void(0);" className="nav-link btn btn-primary header-customize-item button dropdown-toggle"> 
                      <img src={user} alt="" /> Guest
                      </a>
                      <ul className="dropdown-menu">
                        <li className="nav-item">
                          <a onClick={(e) => {e.preventDefault();history.push("/login");}} href="javascript:void(0);" className="nav-link">
                          Login
                          </a>
                        </li>
                        <li className="nav-item">
                          <a onClick={(e) => {e.preventDefault();history.push("/signup");}} href="javascript:void(0);" className="nav-link active">
                          Sign Up
                          </a>
                        </li>
                      </ul>
                    </li>
                  </ul>
                </div>
              </nav>
            </div>
          </div>
        </div>
        <div className="page-title-area">
          <div className="d-table">
            <div className="d-table-cell">
              <div className="container">
                <div className="page-title-content">
                  <h2>Sign Up</h2>
                  <ul>
                    <li><a href="#">Home</a></li>
                    <li>Sign Up</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="login-section ptb-100 bg-l-gry">
          <div className="container">
            <div className="login-form">

            { islogin == false ?
              <div className="login-title">
                <h3>Create an Account!</h3>
                <p>Please fill details to create account.</p>
              </div> : 

              <div className="login-title">
                <h3>Verify Account!</h3>
                <p>Enter the OTP sent to your Email.</p>
              </div>
            }

            { islogin == false ?

              <form noValidate onSubmit={Signup}>

                {formSubmitted && passwordmatchError && <IonText color="danger">
                  <p className="ion-padding-start" style={{'color':'red'}}>
                    *Sorry Password and Confirm Password do not match, Please Try Again!
                  </p>
                </IonText>}

                <div className="row">
                  <div className="col-lg-12">
                      <div className="form-group">
                      <IonInput className="form-control" value={fullname} placeholder="Full Name" onIonChange={e => {
                        setFullname(e.detail.value!);
                        setFullnameError(false);
                      }} required></IonInput>
                      </div>

                      {formSubmitted && fullnameError && <IonText color="danger">
                        <p className="ion-padding-start" style={{'color':'red'}}>
                          *Username is required
                        </p>
                      </IonText>}
                  </div>
                  <div className="col-lg-12">
                      <div className="form-group">
                      <IonInput className="form-control" type="email" value={emailaddress} placeholder="Email Address" onIonChange={e => {
                        setEmailAddress(e.detail.value!);
                        setEmailAddressError(false);
                      }}required></IonInput>
                      </div>

                      {formSubmitted && emailaddressError && <IonText color="danger">
                        <p className="ion-padding-start" style={{'color':'red'}}>
                          *EmailAddress is required
                        </p>
                      </IonText>}


                      {formSubmitted && emailValid && <IonText color="danger">
                        <p className="ion-padding-start" style={{'color':'red'}}>
                          *Email Entered is not valid
                        </p>
                      </IonText>}

                  </div>
                  <div className="col-lg-12">
                      <div className="form-group">
                        <PhoneInput
                          className="form-control"
                          placeholder="Phone Number"
                          value={phonenumber}
                          onChange={(e) => {
                            setPhonenumber(e!);
                            setPhonenumberError(false);
                          }}
                        required
                        />
                      </div>

                      {formSubmitted && phonenumberError && <IonText color="danger">
                        <p className="ion-padding-start" style={{'color':'red'}}>
                          *Phonenumber is required
                        </p>
                      </IonText>}
                  </div>
                  <div className="col-lg-12">
                      <div className="form-group">
                      <IonInput type="password" className="form-control" value={password} placeholder="Password" onIonChange={e => {
                          setPassword(e.detail.value!);
                          setPasswordError(false);
                      }} required></IonInput>
                      </div>

                      {formSubmitted && passwordError && <IonText color="danger">
                        <p className="ion-padding-start" style={{'color':'red'}}>
                          *Password is required
                        </p>
                      </IonText>}
 
                      {formSubmitted && passwordErrorMessage  && <IonText color="danger">
                        <p className="ion-padding-start" style={{'color':'red'}}>
                           *Password not too Strong, Try combinations of min 1 uppercase, 1 lowercase, 1 number, 1 symbol and min length 8 
                         </p>
                       </IonText>}
                  </div>
                  <div className="col-lg-12">
                      <div className="form-group">
                      <IonInput type="password" className="form-control" value={cpassword} placeholder="Confirm Password" onIonChange={e => {
                        setCpassword(e.detail.value!);
                        setCpasswordError(false);
                      }}required></IonInput>
                      </div>
                      {formSubmitted && cpasswordError && <IonText color="danger">
                        <p className="ion-padding-start" style={{'color':'red'}}>
                          *Confirm Password is required
                        </p>
                      </IonText>}
                  </div>
                  <div className="col-lg-12">
                    <div className="send-btn">
                      <IonButton  type="submit"  color="primary" expand="full" shape="round" className="btn-fls">Sign Up Now</IonButton>
                    </div>
                    <span className="block">Already have account? <a onClick={(e) => {e.preventDefault();history.push("/login");}} href="">Login!</a></span>
                  </div>
                </div>
              </form>
            :   

            <form noValidate onSubmit={submitverifications}>

                {formSubmitted && passwordmatchError && <IonText color="danger">
                  <p className="ion-padding-start" style={{'color':'red'}}>
                    *Sorry Password and Confirm Password do not match, Please Try Again!
                  </p>
                </IonText>}

                <div className="row">
                  <div className="col-lg-12">
                      <div className="form-group">
                          <OtpInput
                            onChange={(e:any) => handleChange(e)}
                            inputStyle={{
                              width: 30,
                              color: '#000'
                            }}
                            containerStyle={{
                              justifyContent: 'center'
                            }}
                            value={otp}
                            numInputs={6}
                            separator={<span>-</span>}
                          />
                      </div>

                      {formSubmitted && otperror && <IonText style={{textAlign: 'center'}} color="danger">
                        <p className="ion-padding-start" style={{'color':'red'}}>
                          *OTP is required
                        </p>
                      </IonText>}
 
                  
                  </div>

                  <div className="col-lg-12">
                      <div className="form-group">
                        <p className="resendOtp"  onClick={(e) => resendOtp()}>Resend OTP</p>
                      </div>
                  </div>


                  <div className="col-lg-12">
                    <div className="send-btn">
                      <IonButton  type="submit"  color="primary" expand="full" shape="round" className="btn-fls">Submit</IonButton>
                    </div>
                    <span className="block">Already have account? <a onClick={(e) => {e.preventDefault();history.push("/login");}} href="">Login!</a></span>
                  </div>
                </div>
              </form> }
            </div>
          </div>
        </div>
		<footer id="footer" className="footer-container">
			<div className="container">
				<div className="row list-bunch">
					<div className="col-lg-4 list-bunch-item ">
						<h3 className="logo-title text-white">LookVast</h3>
						<p className="text-left text-white">Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s,</p>
					</div>
					<div className="col-lg-3 list-bunch-item">
						<h6 className="mb-3">Quick Links</h6>
						<ul className="links">
							<li><a onClick={(e) => {e.preventDefault();history.push("/about");}} href="javascript:void(0);">About</a></li>
							<li><a onClick={(e) => {e.preventDefault();history.push("/why-choosing-us");}} href="javascript:void(0);">Why Choosing us</a></li>
							<li><a onClick={(e) => {e.preventDefault();history.push("/faq");}} href="javascript:void(0);">FAQ</a></li>
							<li><a onClick={(e) => {e.preventDefault();history.push("/contact");}} href="javascript:void(0);">Contact Us</a></li>
							<li><a onClick={(e) => {e.preventDefault();history.push("/privacy-notice");}} href="javascript:void(0);">Privacy Notice</a></li>
							<li><a onClick={(e) => {e.preventDefault();history.push("/terms-use");}} href="javascript:void(0);">Terms of use</a></li>
						</ul>
					</div>
					<div className="col-lg-3 list-bunch-item">
						<h6 className="mb-3">Customers</h6>
						<ul className="links">
							<li><a href="javascript:void(0);" onClick={(e) => {e.preventDefault();history.push("/how-it-works");}}>How it works</a></li>
							<li><a href="javascript:void(0);" onClick={(e) => {e.preventDefault();history.push("/service");}}>Find Services</a></li>
							<li><a href="javascript:void(0);">Download App</a></li>
							<li><a onClick={(e) => {e.preventDefault();history.push("/signup");}} href="javascript:void(0);">Sign Up</a></li>
						</ul>
					</div>
					<div className="col-lg-2 list-bunch-item">
						<h6 className="mb-3">Providers</h6>
						<ul className="links">
							<li><a href="javascript:void(0);" onClick={(e) => {e.preventDefault();history.push("/how-works-provider");}}>How does it work</a></li>
							<li><a href="javascript:void(0);">Join our Network</a></li>
							<li><a href="javascript:void(0);">Download App</a></li>
							<li><a onClick={(e) => {e.preventDefault();history.push("/blog");}} href="javascript:void(0);">Blog/Reviews</a></li>
						</ul>
					</div>	
				</div>
			</div>
		</footer>
		<div className="last-footer text-muted">
			<div className="container">
				<div className="row align-items-center">
					<div className="col-lg-6">
          <ul className="social">
							<li><a href="javascript:void(0);"><i className="fab fa-facebook-square"></i></a></li>
							<li><a href="javascript:void(0);"><i className="fab fa-instagram"></i>	</a></li>
							<li><a href="javascript:void(0);"><i className="fab fa-linkedin-in"></i></a></li>
						</ul> 
					</div>
					<div className="col-lg-6 text-right">
						<p> 2021 LookVast. All rights reserved.</p>
					</div>
				</div>
			</div>
		</div>
      </IonContent>
    </IonPage>
  );
};

export default Signup;