  useEffect(() => {

      const callFn = async () => {
        try{
          var subcategories:any = [];
          var data:any = [];
          data = history.location.state;
          console.log(data.selectedcategory);
          setselectedcategories(data.selectedcategory);
          await data.selectedcategory.forEach((element:any,index:any) => {
              axios.post(`${BASE_URL}${GETSUBCATEGORY}`,{
                  'categoryid': element
              },
              {
                    headers: {
                      'Content-Type': 'application/json',
                      Accept: 'application/json',
                    },
              },
              ).then(responseJson => {
                if(responseJson.data.status == 200){
                  if(index != data.selectedcategory.length -1){
                    subcategories.push(responseJson.data.subcategories);
                  }else{
                    subcategories.push(responseJson.data.subcategories);
                    var newlist:any = [].concat(...subcategories);

                    newlist.forEach((element:any, ind:any) => {
                      axios.post(`${BASE_URL}${PROFESSIONALCOUNTBYSUBCAT}`,{
                        subcategoryid: element._id
                      }).then(responseJson => {
                        if(ind != newlist.length - 1){
                          element.service_professionals_count = responseJson.data.count;
                        }else{
                          element.service_professionals_count = responseJson.data.count;
                          setsub_categories(newlist);
                        }
                      })
                    });
                    setTimeout(() => {
                        setloader(false);
                    }, 3000);
                  }

                }else{
                }
              })
          });


          if(localStorage.getItem('customeraddress') != null && localStorage.getItem('customeraddress') != undefined && localStorage.getItem('customeraddress') != ''){
            var add:any = JSON.stringify(localStorage.getItem('customeraddress'));
            var add2 = JSON.parse(add);
            setlocationaddress(JSON.parse(add2).address);
          }

          await getUserDetails();
        }catch(e){
          setloader(false);
          history.push("/error")
        }
      }

      callFn();
      setCount(100);
  }, [count]);

