  /*********ON SUCCESS GEOLOCATION******** */
  const success = (pos:any) => {
    var crd = pos.coords;
    // console.log("Your current position is:");
    // console.log(`Latitude : ${crd.latitude}`);
    // console.log(`Longitude: ${crd.longitude}`);
    // console.log(`More or less ${crd.accuracy} meters.`);
    getAddress(crd.latitude,crd.longitude);
  }
  /*********ON ERRORS GEOLOCATION******** */
  const errors = (err:any) => {
    console.warn(`ERROR(${err.code}): ${err.message}`);
  }

  /**********GET ADDRESS BASE ON COORDINATES******** */

  const getAddress = (lat:any,lng:any) => {
      fetch(`https://maps.googleapis.com/maps/api/geocode/json?latlng=${lat},${lng}&sensor=true&key=AIzaSyBC8I606fPJh8osskxO7U5S_wHmwhvuRj4`).then(response => response.json()).then(json => {
        // console.log(json.results[4].formatted_address);
        setlocationaddress(json.results[4].formatted_address);
        var add = {
          'lat': lat,
          'lng': lng,
          'address': json.results[4].formatted_address
        }
        localStorage.setItem('customeraddress',JSON.stringify(add));
      })
  }


  /********PLACES AUTOCOMPLETE ON CHANGE******** */
  const handleChange = (address:any) => {
    setlocationaddress(address);
  };
  

  /********PLACES AUTOCOMPLETE ON SELECT******** */
  const handleSelect = (address:any) => {
    setlocationaddress(address);
    geocodeByAddress(address)
      .then((results:any) => getLatLng(results[0]))
      .then((latLng:any) => {
        var add = {
          'lat': latLng.lat,
          'lng': latLng.lng, 
          'address': address
        }
        localStorage.setItem('customeraddress',JSON.stringify(add));
      }).catch((error:any) => console.error('Error', error));
  };



