import {
	IonButtons,
	IonContent,
	IonHeader,
	IonLabel,
	IonButton,
	IonItem,
	IonInput,
	IonCheckbox,
	IonMenuButton,
	IonPage,
	IonTitle,
	IonToolbar,
	IonModal
} from "@ionic/react";
import { useParams } from "react-router";
import React, { useEffect, useState } from "react";
import ExploreContainer from "../components/ExploreContainer";
// import "./about.css";
import flag1 from "../img/french-polynesia.svg";
import flag2 from "../img/en.svg";
import user from "../img/user.svg";
import summary1 from "../img/proffessional-4.jpg";
import summary2 from "../img/overview.jpg";
import summary3 from "../img/about.jpeg";
import summary4 from "../img/about-sec1.png";
import summary5 from "../img/avatar.jpg";
import shape from "../img/shape.png";
import userimg from "../img/user.svg";
import overview from "../img/about2.jpeg";
import handshake from "../img/handshake.png";
import counting from "../img/counting.png";
import speak from "../img/speak.png";
import avatar from "../img/proffessional-2.jpg";
import { useHistory } from "react-router";
import { BASE_URL, GETPROFESSIONAL, BASE_URL_IMAGES } from "../util/constants";
import axios from "axios";

var neworders: any = "0";
const Sidebar: React.FC = () => {
	const history = useHistory();
	const [ showdetails, setshowdetails ] = useState<any>("");
	const [ count, setCount ] = useState(0);

	useEffect(
		() => {
			axios
				.post(
					`${BASE_URL}${GETPROFESSIONAL}`,
					{
						id: localStorage.getItem("SP_ID")
					},
					{
						headers: {
							"Content-Type": "application/json",
							Accept: "application/json"
						}
					}
				)
				.then((responseJson) => {
					if (responseJson.data.status == 200) {
						setshowdetails(responseJson.data.data);
					}
				});
			setCount(100);
		},
		[ count ]
	);

	return (
		<div className="col-md-4 col-lg-3">
			<div className="sidebar-wraper">
				<div className="sidebar-bg-image" />
				<div className="user-sidebar">
					<div className="avatar-upload">
						<div className="avatar-preview">
							{showdetails.image != null && showdetails.image != undefined && showdetails.image != "" ? (
								<div
									className="imagePreview scnd-im"
									style={{ backgroundImage: `url(${BASE_URL_IMAGES}${showdetails.image})` }}
								/>
							) : (
								<div className="imagePreview" />
							)}
						</div>
					</div>
					<h4>{showdetails.companyname}</h4>
					<p>{showdetails.email}</p>
				</div>
				<ul>
					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push("/professional-dashboard");
							}}
							className="active"
						>
							{" "}
							<i className="fas fa-home" />Dashboard
						</a>{" "}
					</li>
					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push("/job-orders");
							}}
						>
							<i className="fas fa-briefcase" /> Job Orders
						</a>
					</li>
					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push("/calendar");
							}}
						>
							<i className="far fa-calendar-alt" /> Availability Calendar{" "}
						</a>
					</li>
					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push("/professional-orders");
							}}
						>
							<i className="fas fa-layer-group" /> My Orders
						</a>
					</li>
					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push("/professional-quotes");
							}}
						>
							<i className="fas fa-layer-group" /> Requested Quotes
						</a>
					</li>

					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push("/professional-quote-replies");
							}}
						>
							<i className="fas fa-layer-group" /> My Quote replies
						</a>
					</li>

					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push("/professional-messages");
							}}
						>
							<i className="far fa-envelope" />Messages
						</a>
					</li>
					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push("/customer-reviews");
							}}
						>
							<i className="far fa-star" />Customer Reviews
						</a>
					</li>
					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push("/professional-transactions");
							}}
						>
							<i className="far fa-money-bill-alt" />Transactions
						</a>
					</li>
					{/* <li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push("/professional-paycards");
							}}
						>
							<i className="far fa-credit-card" />Payment Cards
						</a>
					</li> */}
					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push("/professional-notifications");
							}}
						>
							<i className="far fa-bell" />Notifications
						</a>
					</li>
					<li>
						<a
							href="javascript:void(0);"
							onClick={(e) => {
								e.preventDefault();
								history.push("/professional-settings");
							}}
						>
							<i className="fas fa-user-cog" />Profile Settings
						</a>
					</li>
				</ul>
			</div>
		</div>
	);
};

export default Sidebar;
