import React from 'react';
import { Route, Redirect } from "react-router-dom";

const GuardedRoute = ({ component: Component, auth, ...rest }) => {
console.log("auth:" + auth)
    return(
        <Route {...rest} render={(props) => (
            auth === true
                ? <Redirect to='/home' />
                : <Component {...props} />
        )} />
    )
}

export default GuardedRoute;