import {
	IonButtons,
	IonContent,
	IonHeader,
	IonLabel,
	IonButton,
	IonItem,
	IonInput,
	IonCheckbox,
	IonMenuButton,
	IonPage,
	IonTitle,
	IonToolbar,
	IonModal
} from "@ionic/react";
import { useParams } from "react-router";
import React, { useEffect, useState } from "react";
import ExploreContainer from "../components/ExploreContainer";
import flag1 from "../img/french-polynesia.svg";
import flag2 from "../img/en.svg";
import user from "../img/user.svg";
import summary1 from "../img/proffessional-4.jpg";
import summary2 from "../img/overview.jpg";
import summary3 from "../img/about.jpeg";
import summary4 from "../img/about-sec1.png";
import summary5 from "../img/avatar.jpg";
import shape from "../img/shape.png";
import userimg from "../img/user.svg";
import overview from "../img/about2.jpeg";
import handshake from "../img/handshake.png";
import counting from "../img/counting.png";
import speak from "../img/speak.png";
import avatar from "../img/proffessional-2.jpg";
import { useHistory } from "react-router";
import { BASE_URL, BASE_URL_IMAGES, GETUSERBYID, GETCATEGORIES } from "../util/constants";
import axios from "axios";

var neworders: any = "0";

interface IfirstChildProps {
	lang: string;
	updateLanguage: (arg: string) => void;
}

const UserHeader: React.FC<IfirstChildProps> = ({ lang, updateLanguage }: IfirstChildProps) => {
	const history = useHistory();
	const [ showdetails, setshowdetails ] = useState<any>("");
	const [ count, setCount ] = useState(0);
	const [ page, setpage ] = useState<any>();
	const [ categorieslist, setCategoriesList ] = useState([]);
	const [ language, setlanguage ] = useState("fr");
	const [ service, setService ] = useState<string>();
	const [ HASLOGGEDIN, SETHASLOGGEDIN ] = useState<any>(false);

	useEffect(() => {
		var lang = localStorage.getItem("lookvast_lang");
		var has_loggedin = localStorage.getItem("HAS_LOGGED_IN");
		SETHASLOGGEDIN(has_loggedin);
		if (lang != null && lang != undefined && lang != "") {
			setlanguage(lang);
		}
	}, []);

	useEffect(
		() => {
			var data: any;
			data = history.location.state;
			if (data == undefined) {
				setpage("dashboard");
			} else {
				setpage(data.pagename);
			}

			axios
				.post(
					`${BASE_URL}${GETUSERBYID}`,
					{
						userid: localStorage.getItem("USER_ID")
					},
					{
						headers: {
							"Content-Type": "application/json",
							Accept: "application/json"
						}
					}
				)
				.then((responseJson) => {
					if (responseJson.data.status == 200) {
						setshowdetails(responseJson.data.data);
					}
				});
			_getCat();
			setCount(100);
		},
		[ count ]
	);

	const _getCat = () => {
		fetch(`${BASE_URL}${GETCATEGORIES}`).then((response) => response.json()).then((json) => {
			if (json.status == 200) {
				var data = [];
				data = json.categories;
				setCategoriesList(data);
			}
		});
	};

	return (
		<div className="navbar-area bg-white">
			<div className="trifles-responsive-nav">
				<div className="container">
					<div className="trifles-responsive-menu">
						<div className="logo">
							<a href="javascript:void(0);">
								<h3 className="logo-title">LookVast</h3>
							</a>
						</div>
					</div>
				</div>
			</div>
			<div className="trifles-nav">
				<div className="container-fluid px-5">
					<nav className="navbar navbar-expand-md navbar-light">
						<a
							className="navbar-brand"
							onClick={(e) => {
								e.preventDefault();
								history.push("/home");
							}}
							href="javascript:void(0);"
						>
							<h3 className="logo-title">LookVast</h3>
						</a>
						<div className="collapse navbar-collapse mean-menu" id="navbarSupportedContent">
							<ul className="navbar-nav">
								{/* <li className="nav-item active">
									<a
										href="javascript:void(0);"
										className="nav-link"
										onClick={(e) => {
											e.preventDefault();
											history.push("/home");
										}}
									>
										Maison
									</a>
								</li> */}
								<li className="nav-item">
									<a href="javascript:void(0);" className="nav-link">
										Aucciel
										<i className="fa fa-chevron-down" />
									</a>
									<ul className="dropdown-menu">
										<li className="nav-item">
											<a
												className="nav-link active"
												onClick={(e) => {
													e.preventDefault();
													history.push("/about");
												}}
											>
												About
											</a>
										</li>
										<li className="nav-item">
											<a
												onClick={(e) => {
													e.preventDefault();
													history.push("/privacy-notice");
												}}
												className="nav-link"
											>
												Privacy
											</a>
										</li>
										<li className="nav-item">
											<a
												onClick={(e) => {
													e.preventDefault();
													history.push("/contact");
												}}
												className="nav-link"
											>
												Contact Us
											</a>
										</li>
									</ul>
								</li>
								{/* <li className="nav-item">
									<a href="javascript:void(0);" className="nav-link">
									A Propos De
									</a>
									</li> 
								*/}
								<li className="nav-item">
									<a href="javascript:void(0);" className="nav-link">
										Explorer Les Services
										<i className="fa fa-chevron-down" />
									</a>
									<ul className="dropdown-menu">
										{categorieslist.length > 0 ? (
											categorieslist.map((cat: any) => {
												return (
													<li className="nav-item">
														{language == "fr" ? (
															<a
																onClick={() => {
																	var arr = [];
																	arr.push(cat._id);
																	history.push({
																		pathname: "/categories",
																		state: { selectedcategory: arr }
																	});
																}}
																style={{ cursor: "pointer" }}
																className="nav-link"
															>
																{cat.categoryname}
															</a>
														) : (
															<a
																onClick={() => {
																	var arr = [];
																	arr.push(cat._id);
																	history.push({
																		pathname: "/categories",
																		state: { selectedcategory: arr }
																	});
																}}
																style={{ cursor: "pointer" }}
																className="nav-link"
															>
																{cat.categorynameen}
															</a>
														)}
													</li>
												);
											})
										) : null}
									</ul>
								</li>
								{/* <li className="nav-item">
								<a href="javascript:void(0);" className="nav-link">
								Explorer Les Services
								</a>
								</li> */}
								<li className="nav-item">
									<a onClick={() => history.push("/add-business")} className="nav-link">
										Jainces Pro
									</a>
								</li>
							</ul>
							<ul className="navbar-nav mr-0 right-menu">
								<li className="nav-item">
									{language == "fr" ? (
										<a
											href="javascript:void(0);"
											className="nav-link btn btn-primary header-customize-item button langauge-dropdown dropdown-toggle"
										>
											<img src={flag1} alt="Logo" />
											{/* Fr */}
										</a>
									) : (
										<a
											href="javascript:void(0);"
											className="nav-link btn btn-primary header-customize-item button langauge-dropdown dropdown-toggle"
										>
											<img src={flag2} alt="Logo" />
											{/* En */}
										</a>
									)}
									<ul className="dropdown-menu">
										<li className="nav-item">
											<a
												onClick={() => {
													setlanguage("fr");
													localStorage.setItem("lookvast_lang", "fr");
													updateLanguage("fr");
												}}
												className={
													language == "fr" ? "nav-link active langdrop" : "nav-link langdrop"
												}
											>
												<img src={flag1} alt="Logo" />
												{/* Fr */}
											</a>
										</li>
										<li className="nav-item">
											<a
												onClick={() => {
													setlanguage("en");
													localStorage.setItem("lookvast_lang", "en");
													updateLanguage("en");
												}}
												href="javascript:void(0);"
												className={
													language == "en" ? "nav-link active langdrop" : "nav-link langdrop"
												}
											>
												<img src={flag2} alt="Logo" />
												{/* En */}
											</a>
										</li>
									</ul>
								</li>
								<li className="nav-item userDashboardProfileDropdown">
									<a
										href="javascript:void(0);"
										className="nav-link btn btn-primary header-customize-item button dropdown-toggle"
									>
										{showdetails.image != null &&
										showdetails.image != "" &&
										showdetails.image != undefined ? (
											<img src={`${BASE_URL_IMAGES}${showdetails.image}`} alt="" />
										) : (
											<img src={`${BASE_URL_IMAGES}1624632511823download2.jpeg`} alt="" />
										)}
										{showdetails.fullname}
									</a>

									{HASLOGGEDIN ? (
										<ul className="dropdown-menu">
											<li className="nav-item">
												<a
													onClick={(e) => {
														e.preventDefault();
														history.push("/profile-settings");
													}}
													style={{ cursor: "pointer" }}
													className="nav-link active"
												>
													Profile Settings
												</a>
											</li>
											<li className="nav-item">
												<a
													onClick={(e) => {
														e.preventDefault();
														localStorage.removeItem("USER_ID");
														localStorage.removeItem("HAS_LOGGED_IN");
														history.push("/login");
													}}
													style={{ cursor: "pointer" }}
													className="nav-link"
												>
													Logout
												</a>
											</li>
										</ul>
									) : (
										<ul className="dropdown-menu">
											<li className="nav-item">
												<a
													onClick={(e) => {
														e.preventDefault();
														history.push("/login");
													}}
													href="javascript:void(0);"
													className="nav-link"
												>
													Login
												</a>
											</li>
											<li className="nav-item">
												<a
													onClick={(e) => {
														e.preventDefault();
														history.push("/signup");
													}}
													href="javascript:void(0);"
													className="nav-link active"
												>
													Sign Up
												</a>
											</li>
										</ul>
									)}
								</li>
							</ul>
						</div>
					</nav>
				</div>
			</div>
		</div>
	);
};

export default UserHeader;
